import { Component, OnInit ,Inject } from '@angular/core';
import { TranslateService } from 'src/app/services/translate/translate.service';
import { SharedService } from 'src/app/services/shared.service';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';

import * as $ from 'jquery';
import * as AOS from 'aos';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';


export interface DialogData {
  url: '';
  code: '';
  name: '';
}


@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
  
export class ProductsComponent implements OnInit {
  selectedValue: any;
  httpHeaders = new HttpHeaders({
    'Content-Type': 'application/json',
    'Cache-Control': 'no-cache'
  });

  metals=['Gold','Silver','Diamond']
  sub;
  metal;


  openDialog(item) {
    console.log(item.imgsrc);
    this.dialog.open(DialogDataExampleDialog, {
      data: {
        url: item.imgsrc,
        name: item.name,
        code: item.code
      },
      height: 'auto',
      width:'80vh'
    });
  }
  options = {
    headers: this.httpHeaders
  };

  jsondata: any[]= [];
  value: any ='allproducts';
  tempdata: any[] = [];
  constructor(private ts:TranslateService,private sh:SharedService,private route: ActivatedRoute,
    private router: Router, private http:HttpClient,public dialog: MatDialog) { }

  aosInit()
  {
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
    });
    
    AOS.init({
      duration:1200
    });

  }
  ngOnInit() {
   
    window.onscroll = function() {myFunction()};

    var header = (document.getElementById("selectedJewell"))as HTMLElement;
    var sticky = header.offsetTop;
    
    function myFunction() {
      console.log(sticky);
    console.log(window.pageYOffset);
      if (window.pageYOffset > sticky+370) {
        header.classList.add("fixeddiv")
       // header.className += "fixeddiv";
      } else {
        header.classList.remove("fixeddiv")
      // header.className="selectedJewell"
      }
    }



    this.aosInit();
    
    
    if(this.sh.getValue())
      this.value = this.sh.getValue();
    console.log(this.value);

    this.getAllProducts().subscribe(data => {
      this.jsondata = data;
      this.tempdata = this.jsondata;
      if (!this.metals.includes(this.value)) {
        (document.getElementById(this.value) as HTMLInputElement).checked = true;
      }
      else {
        this.tempdata = this.tempdata.filter(data => data.metal == this.value);
        (document.getElementById('allproducts') as HTMLInputElement).checked = true;
        this.value ='allproducts';
      }
      console.log(this.tempdata);
    });
   

  }
  ngAfterViewInit() {
    $('#exampleModal').on('show.bs.modal', function (event) {
      var button = $(event) // Button that triggered the modal
      var recipient = button.data('whatever') // Extract info from data-* attributes
      // If necessary, you could initiate an AJAX request here (and then do the updating in a callback).
      // Update the modal's content. We'll use jQuery here, but you could use a data binding library or other methods instead.
      console.log(recipient);
      var modal = $(this)
      modal.find('.modal-title').text('New message to ' + recipient)
      modal.find('.modal-body input').val(recipient)
    });
  }
 

  getAllProducts(): Observable<any> 
  {
    return this.http.get('./assets/products.json', this.options);
  }

  handleChange(event)
  {
    this.aosInit();
    this.value = event;
    this.selectedValue = event;
    if (this.selectedValue == 'allproducts')
    {
      this.tempdata = this.jsondata;
    }
    else
    {
      this.tempdata = this.jsondata.filter(data => {
       return  data.category.toLowerCase() == this.selectedValue;
      });
      console.log(this.tempdata);
      }
  }

 
  

}



@Component({
  selector: 'dialog-data-example-dialog',
  templateUrl: 'dialog-data-example-dialog.html',
})
  
export class DialogDataExampleDialog {
  constructor(@Inject(MAT_DIALOG_DATA) public data: DialogData) {}
}
